"2025-06-12": {
  title: "A Moment for Haiku",
  blocks: [
    {
      type: "text",
      value: `Today, I want to share something a little different: a haiku.

A haiku is a traditional Japanese poem, famous for its three-line structure and its focus on simplicity and precision. But beyond the 5-7-5 syllable rule, the real essence of haiku is about capturing a fleeting momenta blend of what we see and what we feel, distilled into just a few words. Haikus invite us to pause, notice the small details, and find beauty or meaning in the ordinary. Theyre like tiny windows into the world, where a single image or sensation can hint at something much larger.

Rooted in Zen philosophy, haiku encourages us to let go of overthinking and just experience the present, allowing the reader to find their own meaning in the space between the words. Its not about explaining everything, but about suggesting a feeling or a connectionbetween people, nature, and life itself.

So, heres a haiku I wrote for you, and of course about you ( This time no AI was used at all):


Veil of quiet strength  
Silk and fire in her gaze  
Gentle, wild, and true


Let this small poem be a moment of reflection, a little pause in your day, and a reminder of the beauty found in simplicity.`
    }
  ]
},
"2025-06-13": {
  title: "A Story of Growth: The Path Back to Myself",
  blocks: [
    {
      type: "text",
      value: `Today I want to share a very personal story about my life and the journey of growth I've experienced over the past few years. Honestly, if I hadnt gone through this path, none of thisthis calendar, my note with the flowers, or even half of our conversationswould probably exist.

This is a story I havent really told anyone in full before. Of course, my friends and family know bits and pieces, but I dont think Ive ever shared the whole thingexcept with my therapist, who, as youll see, played a very important role!

It starts with me as a child (dont worry, nothing bad happened to me in church). I was a very sensitive kid. I remember crying a lot when something upset me, and feeling unable to respond in argumentsmy throat would tighten up and Id just start to cry. I wasnt used to violence; my parents never hit me (which was actually common in Argentina back then), and they rarely even raised their voices. If I misbehaved, my punishments were things like no TV or no video games.

None of this was ever a problem until I started high school. I was in an all-boys class35 boys, all together from age 12 to 18. As you might imagine, it wasnt exactly a place where showing feelings was encouraged. It wasnt the ideal environment for a sensitive boy. I never had social problems or bullying, because Ive always been good at adapting. So I did what I had to do: I learned to stop feeling so much and started building walls around myself. I got very good at it, and honestly, I had a great time in high school and made a lot of friendseight of whom Im still close with today.

But all of this came at a cost. Something I only realized a few years ago: I paid for it by becoming numb. I lost the ability to really feel, and even to recognize what I was feeling.

A lot of time passed. I had several long relationships, but I went through them completely numb. Until one day in Barcelona, things started to changewithout me even realizing it.

This was about five years ago, when my friend Fran invited me to my first electronic music party. At that party, and later, he introduced me to a drug called MDMA (the main ingredient in ecstasy pills). Its a very curious drugnothing like what Id imagined. Its called an empathogen, and it makes you feel lovea lot of it. For me, it was a feeling I hadnt experienced in a very long time, but it was so strong and familiar that I recognized it immediately.

(Just a note: this is an illegal drug, but in the last decade, psychiatrists have started to seriously investigate its use in treatmentsespecially for things like PTSDbecause it can be incredibly helpful in some cases.)

Of course, the drug didnt fix me. But it was the trigger that made me start asking myself questions. A few months later, I decided to begin therapysomething Id never considered before and honestly had a lot of prejudices about.

Since then, the journey hasnt been easy. I was like a child, learning about feelings for the first time. And when I finally opened the gates and tore down all the walls Id built, all my emotions came rushing in at once. I had to learn how to handle them and live with themthe good and the bad. It wasnt free; it cost me my longest relationship, which, in hindsight, should have ended much sooner. But it was easy and convenientperfect for the old, numb Javi.

Luckily, things are really good now. The path wasnt easy, but Im truly happy I took it. I dont regret anythingIve recovered my true self. And as I said at the beginning, without this journey, we probably wouldnt be talking right now.

I hope you enjoyed this story, and that it helps you know me a little better.`
    }
  ]
}




